// 进入的第一个页面
 */

<template>
  <div class="top">
    <div class="image" style="width: 100vw">
      <img :src="imgSrc" alt="" />
    </div>
    <van-button class="button" type="info" @click="clickBut()"
      >立即投保</van-button
    >
  </div>
</template>

<script>
// 引入自己写的弹框组件
export default {
  name: "",
  components: {},

  data() {
    return {
      imgSrc: "",
    };
  },

  created() {},
  mounted() {
    // 判断是否有无网
    if (navigator.onLine) {
      this.imgSrc =
        "https://elife.ccb-life.com.cn/statics/cardTemp/cardImg/KDPC006.jpg";
    } else {
      this.imgSrc = require("@/assets/img/KDPC001.jpg");
    }
  },
  methods: {
    // 点击下面的立即投保，弹出弹框
    clickBut() {
      this.$refs.popup.showPopup();
    },
  },
  watch: {},
  computed: {},
};
</script>

<style lang='scss' scoped>
.top {
  height: 100vh;
  width: 100vw;
  img {
    width: 100%;
    height: calc(93.5vh - 4vw);

    /*元素的位置相对于浏览器窗口是固定位置*/
    // position: fixed;
    left: 0;
    top: 0;
  }
}
.button {
  bottom: 0;
  height: 10vw;
  padding: 0 20px;
  font-size: 4.8vw;
  border-radius: 18px;
  width: 100%;

  //   width: 96vw;
  //   position: absolute;
  //   bottom: 2vw;
  //   transform: translateX(2vw);
}
.image {
  height: calc(93.5vh - 4vw);
  overflow-y: auto;
}
</style>
