<template>
  <div>
    <lianxi v-if="1 == 2"> </lianxi>

    <div class="view-box Router">
      <div class="image">
        <div class="van-image loading-image" style="width: 100vw">
          <img :src="imgSrc" class="van-image__img" alt="" />
        </div>
      </div>
      <van-button class="button" type="info" @click="clickBut()"
        >立即投保</van-button
      >
      <popup ref="popup"></popup>
    </div>
  </div>

  <!-- <div class="top">
    <img :src="imgSrc" alt="" />
    <van-button class="button" type="info" @click="clickBut()"
      >立即投保</van-button
    >

    <popup ref="popup"></popup>
  </div> -->
</template>

<script>
// 引入自己写的弹框组件
import popup from "./components/popup.vue";
import lianxi from "./components/lianxi.vue";
export default {
  name: "",
  components: { popup, lianxi },

  data() {
    return {
      imgSrc: "",
    };
  },

  created() {},
  mounted() {
    // 判断是否有无网
    console.log(navigator.onLine,'123');
    if (navigator.onLine) {
      this.imgSrc =
        "https://elife.ccb-life.com.cn/statics/cardTemp/cardImg/KDPC006.jpg";
    } else {
      this.imgSrc = require("@/assets/img/KDPC001.jpg");
    }
  },
  methods: {
    // 点击下面的立即投保，弹出弹框
    clickBut() {
      this.$refs.popup.showPopup();
    },
  },
  watch: {},
  computed: {},
};
</script>

<style lang='scss' scoped>
.top {
  width: 100%;

  overflow-y: auto;

  height: 100%;
  img {
    width: 100%;
    height: calc(93.5vh - 4vw);

    /*元素的位置相对于浏览器窗口是固定位置*/
    // position: fixed;
    left: 0;
    top: 0;
  }
}
.button {
  bottom: 0;
  height: 10vw;
  padding: 0 20px;
  font-size: 4.8vw;
  border-radius: 18px;
  width: 100%;
}

.Router {
  position: absolute;
  left: 0;
  right: 0;
  height: 100vh;
  width: 100vw;
}
.view-box .image {
  height: calc(93.5vh - 4vw);
  overflow-y: auto;
}
</style>
