  // 第一页的弹窗
 */

<template>
  <div>
    <van-popup
      v-model="show"
      closeable
      close-icon="close"
      :close-on-click-overlay="false"
      position="bottom"
      :style="{ height: '50%' }"
    >
      <div class="popup">
        <van-form
          class="form"
          validate-first
          @failed="onFailed"
          @submit="submit"
        >
          <!-- 通过 pattern 进行正则校验 -->
          <span> 身份证号</span>
          <span style="color: red; margin-bottom: 5px">*</span>
          <van-field
            v-model="value1"
            name="身份证号"
            label=""
            placeholder="请输入身份证"
            :rules="[{ validator, message: '身份证号码格式错误！' }]"
            left-icon="contact"
          >
            <!-- 身份证的扫描 -->
            <template #button>
              <van-icon class="scan" name="scan" @click="scanIdCard" />
            </template>
          </van-field>
          <span> 被保人所在学校属于以下那个类型</span>
          <span style="color: red; margin-bottom: 5px">*</span>
          <van-field name="radio" label="" left-icon="home-o">
            <template #input>
              <van-radio-group v-model="radio" direction="horizontal">
                <van-radio
                  v-for="(item, index) of studentList"
                  :key="index"
                  :name="item.studentType"
                  @click="clickRadio(item.tab)"
                >
                  {{ item.studentType }}
                </van-radio>

                <!-- <van-radio name="2">中小学</van-radio>
                <van-radio name="3">幼儿园</van-radio> -->
              </van-radio-group>
            </template>
          </van-field>
          <van-button
            round
            block
            type="primary"
            @click.prevent="value1 = '372522198302043119'"
            >点击此按钮可以填身份证，"懒人用法"</van-button
          >
          <div style="margin: 16px">
            <van-button round block type="info" native-type="submit"
              >提交</van-button
            >
          </div>
        </van-form>
      </div>
    </van-popup>
  </div>
</template>

<script>
import { Toast } from "vant";
import isIdcard from "../JS/index";
export default {
  name: "",
  components: {},

  data() {
    return {
      show: false,
      // 表单
      value1: "",
      value2: "",
      value3: "",
      pattern: false,
      radio: '',

      studentList: [
        {
          studentType: "大学",
          tab: [
            {
              name: "方案一",
              CDPC: "111",
            },
            {
              name: "方案二",
              CDPC: "222",
            },
          ],
        },
        {
          studentType: "小学",
          tab: [
            {
              name: "方案三",
              CDPC: "333",
            },
            {
              name: "方案四",
              CDPC: "444",
            },
          ],
        },
        {
          studentType: "幼儿园",
          tab: [
            {
              name: "方案五",
              CDPC: "333",
            },
            {
              name: "方案六",
              CDPC: "444",
            },
          ],
        },
      ],

      newTba:''
    };
  },

  created() {
    this.radio=this.studentList[0].studentType
    this.newTba=this.studentList[0].tab    
  },
  mounted() {},
  methods: {
    // 点击按钮的时候
    clickRadio(item){
     this.newTba=item        
    },
    // 表单成功的回调

    submit() {
      let string=JSON.stringify(this.newTba)
      this.$router.push({
        path: "/planPage",
        query:{
          tab:string
        }
      });
    },
    // 扫描身份证
    scanIdCard() {
      this.$router.push({
        path: "/scanIdCard",
      });
    },

    // 让弹出框显示

    showPopup() {
      this.show = true;
    },

    // 表单
    // 异步校验函数返回 Promise
    asyncValidator(val) {
      return new Promise((resolve) => {
        Toast.loading("验证中...");

        setTimeout(() => {
          Toast.clear();
          resolve(/\d{6}/.test(val));
        }, 1000);
      });
    },
    onFailed(errorInfo) {
      console.log("failed", errorInfo);
    },
    // 身份证验证
    validator(val) {
      return isIdcard(val);
    },
  },
  watch: {},
  computed: {},
};
</script>

<style lang='scss' scoped>
.form {
  margin-top: 10vh;
}
.popup {
  padding: 15px;
}

:deep(.van-cell) {
  padding: 20px;
}
.scan {
  cursor: pointer;
}
.scan:hover {
  color: red;
  cursor: pointer;
}
</style>
